html {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #ffffff;
  color: #ababab;
}

@media (min-width: 768px) {
  body {
    font-size: 1.2rem;
  }
}

body.no__overflow {
  overflow: hidden;
}

ol,
ul,
li,
a,
p,
span {
  font-size: 1rem;
}

@media (min-width: 768px) {
  ol,
  ul,
  li,
  a,
  p,
  span {
    font-size: 1.2rem;
  }
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: #ababab;
  cursor: pointer;
  outline: none;
  text-decoration: none;
}

a:active, a:hover {
  color: #c5c5c5;
}

img {
  max-width: 100%;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

/* layout */
.page__wrapper {
  padding-left: 15px;
  padding-right: 15px;
}

/* components */
.button {
  text-decoration: none;
  display: inline-block;
  border: 1px solid #ffffff;
  margin: 10px 0;
  padding: 10px 12px;
  text-transform: uppercase;
  font-size: 0.9rem;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.4s ease;
}

.button:hover, .button:active, .button:focus {
  background-color: #000000;
  color: #ffffff;
}

.container__top__banner {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  background-color: #000000;
}

@media screen and (min-width: 768px) {
  .container__top__banner {
    justify-content: flex-end;
    padding-right: 20px;
  }
}

.social {
  padding-right: 10px;
}

@media screen and (min-width: 768px) {
  .social {
    padding-right: 20px;
  }
}

.social .social__icon {
  width: 30px;
}

@media screen and (min-width: 768px) {
  .social .social__icon {
    width: 40px;
  }
}

.search__form {
  display: flex;
  margin: 0;
  padding: 0;
  height: 30px;
}

@media screen and (min-width: 768px) {
  .search__form {
    height: 40px;
  }
}

.search__form .form__input {
  width: 140px;
  height: 100%;
  padding: 5px 10px;
}

.search__form .form__submit {
  width: 30px;
  padding: 5px;
  background-color: #000000;
  color: #ffffff;
}

@media screen and (min-width: 768px) {
  .search__form .form__submit {
    width: 40px;
  }
}

.nav__main {
  padding: 10px 15px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
}

.nav__main > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.nav__main .nav__mobile {
  background: #ffffff;
}

.nav__main .nav__mobile.fixed {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  padding: 10px 15px;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}

@media screen and (min-width: 768px) {
  .nav__main .nav__mobile {
    display: none;
  }
}

.nav__main .nav__items {
  flex-direction: column;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  max-height: 0;
  overflow: hidden;
  text-transform: uppercase;
  background-color: #000000;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}

.nav__main .nav__items.active {
  opacity: 1;
  max-height: 100%;
  transition: opacity 0.4s ease-in-out;
}

@media screen and (min-width: 768px) {
  .nav__main .nav__items {
    position: static;
    flex-direction: row;
    justify-content: space-between;
    max-height: 100%;
    opacity: 1;
    background-color: transparent;
  }
}

.nav__main .nav__items li {
  padding: 16px 20px;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
}

@media screen and (min-width: 768px) {
  .nav__main .nav__items li {
    border-bottom: none;
    padding: 10px 5px;
  }
}

