  .nav__main .nav__items li:nth-of-type(2) {
    border-top: none;
  }
}

.nav__main .nav__items li:last-of-type {
  border-bottom: none;
}

.nav__main .nav__items li.current-cat a {
  color: #0892d0;
}

.nav__main .nav__items .mobile__close {
  border: none;
  align-self: flex-end;
}

@media screen and (min-width: 768px) {
  .nav__main .nav__items .mobile__close {
    display: none;
  }
}

